.portfolio{
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
    flex-direction: column;
    height: 70vh;
    /* margin-top: 80px; */
    /* padding-top: 100px; */
    /* padding-bottom: 150px; */
}
.portfolio span:nth-of-type(1){
    color: var(--black);
    font-size: 1.8rem;
    font-weight: bold;
}
.portfolio span:nth-of-type(2){
    color: var(--orange);
    font-size: 2.8rem;
    font-weight: bold;
}

.portfolio img{
    width: 20rem;
    height: 230px;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
    border-radius: 19px;
}
.grid-container {
    margin-top: 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
  
  .grid-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    
  }
  
  .grid-item a {
    display: block;
  }
  .grid-item img {
    width: 500px;
    height: 250px;
  }
  
  .grid-item div {
    margin-top: 10px;
  }
  
