/* For mobile devices */
@media only screen and (max-width: 767px) {
    .swiper-slide {
      width: 80%;
      margin-right: 0;
    }
  }
  
  /* For tablet devices */
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    .swiper-slide {
      width: 30%;
      margin-right: 30px;
    }
    .swiper-slide:nth-child(2n) {
      margin-right: 0;
    }
  }
  /* For mobile devices */
@media only screen and (max-width: 767px) {
    .swiper-slide {
      width: 100%;
      margin-right: 0;
    }
    .grid-container {
        display: grid;
        width: 90%;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 20px;
      }
      .grid-item a {
        display: block;
        width: 70%;
      }
      img{
        width: 50px;

      }
      #git{
        display: block;
      }
      .portfolio img{
        width: 14rem;
        height: 230px;
        filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
        border-radius: 19px;
        margin-left: -30px;
    }
    .portfolio button{
        /* margin-left: px; */
    }
  }
  
  /* For tablet devices */
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    .grid-container {
        display: grid;
        width: 90%;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
      }
      .portfolio img{
        width: 17rem;
        height: 300px;
        filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
        border-radius: 19px;
        margin-left: 0px;
    }
    .grid-item {
        display: flex;
        flex-direction: column;
        /* align-items: center; */
        
        
      }
  }
  